<template>
  <chart-js-component :data-set="dataSet" :labels="labels" :chart-title="chartTitle" :height="150"/>
</template>

<script>
import _ from "lodash";
import ChartJsComponent from "../common/ChartJsComponent";
import { mapState } from "vuex";
export default {
  name: "DateWisePatientsGraphComponent",
  components: { ChartJsComponent },
  watch: {
    patients: {
      handler: "updateGraphData",
      immediate: true,
      deep: true
    }
  },
  computed: {
    ...mapState(["patients"])
  },
  data: () => ({
    labels: [],
    dataSet: [],
      chartTitle: "   "
  }),
  methods: {
    updateGraphData(patients) {
      if (Array.isArray(patients) && patients.length > 1) {
        // first patient date in seconds
        const firstPatientDate = _.last(patients).infectedDate.seconds;
        // first date
        const firstDate = new Date(firstPatientDate * 1000);
        // today date
        const todayDate = new Date();

        // chart labels
        const labels = [];
        // chart data
        const data = [];

        for (let i = firstDate; i <= todayDate; i.setDate(i.getDate() + 1)) {
          // push each day in labels
          labels.push(
            i.toLocaleDateString("bn-BD", {
              year: "numeric",
              month: "long",
              day: "numeric"
            })
          );

          // each date into unix seconds
          const dateInUNIXSeconds = i.getTime() / 1000;

          // get each day patients
          const eachDayPatients = patients.filter(patient => {
            return patient.infectedDate.seconds === dateInUNIXSeconds;
          });

          // push the number of each day patients
          data.push(eachDayPatients.length);
        }
        // set the labels in data
        this.labels = labels;

        // background color
        const _backgroundColor = [];
        // border color
        const _borderColor = [];
        data.forEach(() => {
          // generate background color
          _backgroundColor.push("rgba(255, 99, 132, 0.6)");
          // generate border color
          _borderColor.push("rgb(255,99,132)");
        });

        this.dataSet = [
          {
            label: " ",
            data: data,
            backgroundColor: _backgroundColor,
            borderColor: _borderColor,
            borderWidth: 1
          }
        ];
      }
    }
  }
};
</script>

